import { Component, OnInit } from '@angular/core';
import { ChartType, ChartOptions } from 'chart.js';
import { SingleDataSet, Label, monkeyPatchChartJsLegend, monkeyPatchChartJsTooltip } from 'ng2-charts';
import { CovidApiService } from '../covid-api.service';

@Component({
  selector: 'app-covid-bar-chart',
  templateUrl: './covid-bar-chart.component.html',
  styleUrls: ['./covid-bar-chart.component.css']
})
export class CovidBarChartComponent implements OnInit {
  public pieChartOptions: ChartOptions = {
    responsive: true,
  };

  // public pieChartLabels: Label[] = [['Total Active', 'Total Deaths', 'Toal Recovered'], ['In', 'Store', 'Sales'], 'Mail Sales'];
   
  public tActive: number;
  public tDeaths: number;
  public tTotal: number;
  public lastupdatedtime: number;
  public tRecovered: number;
  public pieChartLabels: Label[] = [['Total Active'], ['Total Deaths'], ['Toal Recovered']];
  public pieChartColors = [
    {
      backgroundColor: ['rgba(0, 188, 212,1.0)', 'rgba(239, 83, 80,1.0)', 'rgba(76, 175, 80,1.0)'],
    },
  ];
  // public pieChartData: SingleDataSet = [this.tActive, this.tDeaths, this.tRecovered];
    
  public pieChartData: any = [  
    {
        data: []
    }
];
  public pieChartType: ChartType = 'pie';
  public pieChartLegend = true;
  public pieChartPlugins = [];

  constructor(private covidservice: CovidApiService) {
    monkeyPatchChartJsTooltip();
    monkeyPatchChartJsLegend();
  }
  
  getData() {
    this.covidservice.getConfig().subscribe((data: any[]) => {
      // console.log(data['statewise'][0]);
      this.tActive =  data['statewise'][0]['active'];
      this.tDeaths =  data['statewise'][0]['deaths'];
      this.tRecovered =  data['statewise'][0]['recovered'];
      this.pieChartData = [this.tActive, this.tDeaths, this.tRecovered];
      this.tTotal = data['statewise'][0]['confirmed'];
      this.lastupdatedtime = data['statewise'][0]['lastupdatedtime'];

    });
  }

  ngOnInit() {
    this.getData();
  }
}
