import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { CovidBarChartComponent } from './covid-bar-chart/covid-bar-chart.component';
import { StateDashboardComponent } from './state-dashboard/state-dashboard.component';

const routes: Routes = [
  {path: 'india-dashboard', component: CovidBarChartComponent},
  {path: 'state-dashboard', component: StateDashboardComponent},
  {path: '**', component: CovidBarChartComponent}
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
